.visually-hidden {
    visibility: hidden;
}

.advantages {
    background-color: var(--about-bg-color);
    padding-bottom: 60px;
    @media screen and (min-width: 1200px) {
        padding-bottom: 120px;
    }
}

.advantages__list {
    @media screen and (min-width: 768px) {
        display: flex;
    }
}

.advantages__item {
    @media screen and (max-width: 767px) {
        width: 230px;
        margin: 0 auto;
    }

    @media screen and (min-width: 768px) {
        width: calc((100% - 90px) / 3);
    }

    @media screen and (min-width: 1200px) {
        width: calc((100% - 120px) / 3);
    }
}

.advantages__item:not(:last-child) {
    @media screen and (max-width: 767px) {
        margin-bottom: 32px;
    }

    @media screen and (min-width: 768px) {
        margin-right: 30px;
    }

    @media screen and (min-width: 1200px) {
        margin-right: 80px;
    }
}

.advantages__span {
    display: inline-block;
    font-family: 'Titan One', sans-serif;
    font-weight: 400;
    font-size: 36px;
    line-height: 1.14;
    letter-spacing: 0.04em;
    margin-bottom: 20px;
    color: var(--red-color);

    @media screen and (min-width: 768px) {
        margin-bottom: 13px;
        font-size: 48px;
    }

    @media screen and (min-width: 1200px) {
        margin-bottom: 16px;
        font-size: 58px;
    }
}

.advantages__description {
    font-weight: 700;
    font-size: 12px;
    line-height: 1.67;
    letter-spacing: 0.04em;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
        transform: scale(1.2);
        cursor: pointer;
    }

    @media screen and (min-width: 768px) {
        font-size: 14px;
        line-height: 1.71;
    }

    @media screen and (min-width: 1200px) {
        font-size: 16px;
        line-height: 1.75;
    }
}

