.advantages__span-milk::before {
    content: ' ';
    display: block;
    width: 33px;
    height: 50px;
    margin-bottom: 16px;
    background-image: url('../images/advantages/advantages-milk@1x.png');
    background-repeat: no-repeat;
    background-size: contain;

    @media screen and (min-width: 768px) and (max-width: 1199px) {
        margin-bottom: 36px;
    }

    @media screen and (min-width: 1200px) {
        margin-bottom: 40px;
    }
}

.advantages__span-apple::before {
    content: ' ';
    display: block;
    width: 50px;
    height: 50px;
    margin-bottom: 16px;
    background-image: url('../images/advantages/advantages-bucket@1x.png');
    background-repeat: no-repeat;
    background-size: contain;

    @media screen and (min-width: 768px) and (max-width: 1199px) {
        margin-bottom: 36px;
    }

    @media screen and (min-width: 1200px) {
        margin-bottom: 40px;
    }
}

.advantages__span-heart::before {
    content: ' ';
    display: block;
    width: 44px;
    height: 50px;
    margin-bottom: 16px;
    background-image: url('../images/advantages/advantages-heart@1x.png');
    background-repeat: no-repeat;
    background-size: contain;

    @media screen and (min-width: 768px) and (max-width: 1199px) {
        margin-bottom: 36px;
    }

    @media screen and (min-width: 1200px) {
        margin-bottom: 40px;
    }
}